<template>
  <div class="orderManage admin-layout">
    <div class="header">
      <el-form :model="form" ref="form" inline>
        <el-form-item label="订单号" prop="code">
          <el-input v-model="form.code"></el-input>
        </el-form-item>
        <el-form-item label="用户" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="main">
      <el-card>
        <el-table border :data="tableData" style="width: 100%" ref="tableData">
          <el-table-column type="selection" align="center" />
          <el-table-column prop="code" label="订单号" align="center">
          </el-table-column>
          <el-table-column prop="time" label="创建时间" align="center">
            <template slot-scope="{ row }">
              {{ row.time | parseTime }}
            </template>
          </el-table-column>
          <el-table-column prop="date" label="商品" align="center">
            <template slot-scope="{ row }">
              <div class="goods flex-center-y" @click="toDetail(row)">
                <el-image :src="row.url" style="width: 100px"></el-image>
                {{ row.title }}
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="color"
            label="颜色"
            align="center"
            width="80px"
          >
          </el-table-column>
          <el-table-column prop="size" label="尺寸" align="center" width="80px">
          </el-table-column>
          <el-table-column
            prop="price"
            label="单价（元）"
            align="center"
            width="100px"
          >
          </el-table-column>
          <el-table-column
            prop="count"
            label="数量"
            align="center"
            width="80px"
          >
          </el-table-column>
          <el-table-column
            prop="pay"
            label="实付款（元）"
            align="center"
            width="120px"
          >
          </el-table-column>
          <el-table-column prop="state" label="交易状态" align="center">
          </el-table-column>
          <el-table-column prop="remark" label="备注" align="center">
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="{ row }">
              <el-button type="text" @click="confirmOrder(row)">确认订单</el-button>
            </template>
          </el-table-column>
        </el-table>
        <pagination
          :total="total"
          :page.sync="form.page"
          :limit.sync="form.pageSize"
          @pagination="getList"
        />
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  name: 'OrderManage',
  data () {
    return {
      form: {
        code: '',
        username: '',
        page: 1,
        pageSize: 10
      },
      total: 0,
      tableData: [
        {
          id: '7037',
          gid: '7037',
          title: '彪马情侣短T(1)',
          url: require('@/assets/images/200(1).jpg'),
          color: '黑色',
          size: '5XL',
          price: '29',
          count: 1,
          pay: 29,
          state: '已确认',
          remark: '',
          time: Date.now(),
          code: '2020070210543186802' // 订单号
        }
      ]
    }
  },
  methods: {
    search () {
      this.form.page = 1
      this.getList()
    },
    reset () {
      this.$refs.form.resetFields()
    },
    getList () {
      console.log('getList')
    },
    add () {
      console.log('新增')
    },
    check (row) {
      console.log('查看')
    },
    edit (row) {
      console.log('编辑')
    },
    del (row) {
      console.log('删除')
    },
    toDetail (row) {
      this.$router.push({ path: '/details', query: { gid: row.gid } })
    },
    confirmOrder (row) {
      console.log('确认订单')
    }
  }
}
</script>
<style lang="scss" scoped>
.orderManage {
  .table-div {
    padding: 20px;
    .table {
      .table-title {
        padding: 12px 0;
        .time,
        .number-value {
          margin-right: 20px;
        }
        .number-value {
        }
      }
      .goods {
        cursor: pointer;
        &:hover {
          color: $color_primary;
        }
      }
    }
  }
}
</style>
